body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 600px) {
  .container {
    grid-template-areas: "header header header header" "selected-heroes selected-heroes selected-heroes active-list" "active-synergies active-synergies active-synergies active-list" "all-synergies all-synergies all-synergies active-list" "all-heroes all-heroes all-heroes all-heroes" "footer footer footer footer";
    grid-template-columns: 1fr 1fr 200px 135px;
    grid-template-rows: 60px 225px 225px 315px 1fr 80px; }
  .hero {
    flex: 0 1 85px !important; }
  .all-synergies {
    background: #4A6D63 !important; }
  .active-list {
    font-size: 14px; }
  .active-list > div, .all-synergies > div, .active-synergies > div {
    margin-bottom: 15px;
    padding: 3px !important;
    box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.75); } }

@media only screen and (min-width: 600px) {
  .container {
    grid-template-areas: "header header header header" "active-list selected-heroes active-synergies all-synergies" "active-list selected-heroes active-synergies all-synergies" "all-heroes all-heroes all-heroes all-heroes" "footer footer footer footer";
    grid-template-columns: 200px 1fr 1fr 1fr;
    grid-template-rows: 60px 1fr 1fr 1fr 60px; } }

.all-heroes {
  padding-bottom: 10px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: flex-end; }
  .all-heroes .hero {
    flex: 0 1 8.916%;
    display: flex;
    flex-flow: column; }

.hero:hover {
  opacity: .8; }

.hero img {
  width: 100%; }

.selected img {
  -webkit-filter: grayscale(100%) brightness(40%) sepia(100%) hue-rotate(-50deg) saturate(600%) contrast(0.8);
          filter: grayscale(100%) brightness(40%) sepia(100%) hue-rotate(-50deg) saturate(600%) contrast(0.8); }

.disabled img {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%); }

.header, .footer {
  background: #1e2a38;
  color: white; }
  .header strong, .footer strong {
    margin: 0;
    display: block; }

.active-list, .all-synergies {
  background: #4D5B6C;
  color: white; }

.all-heroes {
  background: #081320; }

.selected-heroes {
  background: white;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.9) inset; }

.active-synergies {
  background: white;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.9) inset; }

.active-list, .all-synergies, .active-synergies {
  text-transform: capitalize; }
  .active-list > strong, .all-synergies > strong, .active-synergies > strong {
    padding-bottom: 10px;
    display: block; }
  .active-list > div, .all-synergies > div, .active-synergies > div {
    margin-bottom: 15px;
    padding: 5px;
    box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.75); }
  .active-list .selected, .all-synergies .selected, .active-synergies .selected {
    color: #1e2a38;
    font-weight: 500; }

html,
body,
#root {
  width: 100%;
  height: 100%; }

.container {
  display: grid;
  width: 100%;
  height: 100%; }

.container > div {
  padding: 10px; }

.header {
  grid-area: header;
  display: flex;
  align-items: center; }

.selected-heroes {
  grid-area: selected-heroes;
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  overflow-y: scroll; }
  .selected-heroes .hero {
    flex: 0 1 110px; }

.active-list {
  grid-area: active-list;
  overflow-y: scroll; }

.active-synergies {
  grid-area: active-synergies;
  overflow-y: scroll; }

.all-synergies {
  grid-area: all-synergies;
  overflow-y: scroll; }

.all-heroes {
  grid-area: all-heroes; }

.footer {
  grid-area: footer; }

